import React, { useState, useEffect } from 'react'
import { useLocation } from 'react-router'

import { getAPI } from '../../utils/fetchData'
import { IBlog } from '../../utils/typeScript'

import CardHoriz from '../cards/cardHoriz'

const Search = () => {
    const [search, setSearch] = useState('')
    const [blogs, setBlogs] = useState<IBlog[]>([])

    const { pathname } = useLocation()

    useEffect(() => {
      const delayDebounce = setTimeout(async () => {
        if(search.length < 2) return setBlogs([])

        try {
          const res = await getAPI(`search/blogs?title=${search}`)
          setBlogs(res.data)
        } catch (err: any) {
          console.log(err)
        }
      }, 400)

      return () => clearTimeout(delayDebounce)
    },[search])

    useEffect(() => {
      setSearch('')
      setBlogs([])
    },[pathname])

    return (
        <div className='search w-100 position-relative me-4'>
          <input type='text' className='form-control me-2 w-100 rounded' 
          value={search} placeholder='Nhp mc bn mun tm kim...' 
          onChange={e => setSearch(e.target.value)} />

          {
            search.length >= 2 &&
            <div className='position-absolute pt-2 px-1'
            style={{ 
              background: '#eee', 
              zIndex: 10, 
              maxHeight: 'calc(100vh - 100px)', 
              overflow: 'auto' 
            }}>
              {
                blogs.length
                ? blogs.map(blog =>(
                  <CardHoriz key={blog._id} blog={blog} />
                ))
                : <h3 className='text-center'>Khng c bi vit no c tm thy</h3>
              }
            </div>
          }  
        </div>
    )
}

export default Search
