import { IUserRegister, IBlog } from './typeScript'

export const validRegister = ( userRegister: IUserRegister) => {
    const { name, account, password, cf_password } = userRegister;
    const errors: string[] = [];

    if(!name) {
        errors.push('Vui lng nhp h v tn.')
    }else if(name.length > 20) {
        errors.push('H v tn ch di ti a 20 k t!') 
    }

    if(!account) {
        errors.push('Nhp a ch email hoc s in thoi.')
    }else if(!validPhone(account) && !validateEmail(account)) {
        errors.push('a ch email hoc s in thoi khng ng, xin vui lng nhp li!') 
    }

    const msg = checkPassword(password, cf_password)
    if(msg) errors.push(msg)
    
    return {
        errMsg: errors,
        errLength: errors.length
    }  
}

export const checkPassword = (password: string, cf_password: string) => {
    if(password.length < 6){
        return ("Mt khu phi di t nht 6 k t!")
    }else if(password !== cf_password){
        return ('Mt khu khng trng khp, xin hy nhp li!')
    }
}

export function validPhone(phone: string) {
    const re = /^[+]/g
    return re.test(phone)
}
  
export function validateEmail(email: string) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

//Valid Blog
export const validCreateBlog = ({
    title, content, description, thumbnail, category
}: IBlog) => {
    const err: string[] = []

    if(title.trim().length < 10){
        err.push('Tiu  phi di t nht 10 k t.')
    }else if(title.trim().length > 50){
        err.push('Tiu  ch di ti a 50 k t.')
    }

    if(content.trim().length < 50){
        err.push('Ni dung phi di t nht 50 k t.')
    }else if(content.trim().length > 2000){
        err.push('Ni dung ch di ti a 2000 k t.')
    }

    if(description.trim().length < 10){
        err.push('M t phi di t nht 10 k t.')
    }else if(description.trim().length > 2000){
        err.push('M t ch di ti a 2000 k t.')
    }

    if(!thumbnail){
        err.push('nh khng c  trng.')
    }

    if(!category){
        err.push('Th loi khng c  trng.')
    }

    return {
        errMsg: err,
        errLength: err.length
    }
    
}

// Shallow equality
export const shallowEqual = (object1: any, object2: any) => {
    const keys1 = Object.keys(object1)
    const keys2 = Object.keys(object2)

    if(keys1.length !== keys2.length) {
        return false
    }

    for(let key of keys1) {
        if(object1[key] !== object2[key]){
            return false
        }
    }

    return true
}